<template>
  <el-container>
  <el-tabs v-model="activeName" >
    <el-tab-pane label="算法实现" name="first">
      <MarkdownReader :url="url"></MarkdownReader>
    </el-tab-pane>
    <el-tab-pane label="算法调用" name="second">
      <el-container>
        <el-form ref="form" :model="form" label-width="80px" :inline="false">
          <el-form-item label="输入文件">
            <el-select v-model="form.inputfile" placeholder="请选择输入文件">
              <el-option
                v-for="item in options"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="输出文件">
            <el-input v-model="form.outputfile"></el-input>
          </el-form-item>
          <el-form-item lable="输出方式">
            <el-select v-model="form.function" placeholder="请选择输出方式">
              <el-option label="输出中位数" value="Median"></el-option>
              <el-option label="百分位数对应的关键字值" value="Keyword"></el-option>
              <el-option label="统计任意给定的两个关键字区段间数据所占的比例" value="KeywordPercent"></el-option>
              <el-option label="去除异常数据" value="EliminateAbnormalData"></el-option>
              
            </el-select>
          </el-form-item>
          <el-form-item label="X">
              <el-input v-model="form.X"></el-input>
          </el-form-item>
          <el-form-item label="Y">
              <el-input v-model="form.Y"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建任务</el-button>
            <el-button>取消</el-button>
          </el-form-item>
      </el-form>
      </el-container>
      <el-container>
        <!-- <DataTable :books="books"></DataTable> -->
      </el-container>
    </el-tab-pane>
    <el-tab-pane name="third" disabled>
      <span slot="label">
            {{algorithm}}
      </span>
    </el-tab-pane>
    
  </el-tabs>
  </el-container>
</template>
<script>
let _this = this
  export default {
    data() {
      return {
        activeName: 'first',
        algorithm:'BFPTR',
        url: 'T5',
        form:{
          inputfile:"",
          outputfile:"",
          function:"",
          X:1
        },
        books:[{},
          {
            "bookName":"123",
            "bookId":"123123",
            "author":"asdasd",
            "monthTicket":100
          }
        ],
        options:[]
      };
    },
    methods: {
      chooseAlgorithm(command) {
          this.algorithm=command
        
      },
      onSubmit(){
        this.form.algorithm=this.algorithm
        this.$axios.post('/api/sorting',this.form).then((res)=>{
          console.log(res)
        }).catch((res)=>{
          console.log(res)
        })
      }
    },
    created(){
      this.chooseAlgorithm('BFPTR')
      this.$axios.get('/api/listJson').then((res)=>{
        this.options = res.data
      })
    },
  };
</script>


